.BoardCell {
	background-color: #caebf9;
	color: #666;
	padding: 25px;
	margin: 20px;
	cursor: pointer;
}

.BoardCell.movedTo{
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-name: fadeIn;
	        animation-name: fadeIn;
}

.BoardCell.movedFrom{
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-name: fadeOut;
	        animation-name: fadeOut;
}

@-webkit-keyframes fadeIn{
	from{
		opacity: 0.01;
	}

	to{
		opacity: 1;
	}
}

@keyframes fadeIn{
	from{
		opacity: 0.01;
	}

	to{
		opacity: 1;
	}
}

@-webkit-keyframes fadeOut{
	from{
		opacity: 0.01;
	}

	to{
		opacity: 1;
	}
}

@keyframes fadeOut{
	from{
		opacity: 0.01;
	}

	to{
		opacity: 1;
	}
}



